	module.exports = {"data":{"markdownRemark":{"html":"<p>「あの…」 僕は自宅で古道具屋をやっている手前いろんな客が来る。時には人間以外、そう妖怪や亡霊の類までやってきたこともあるくらいだ。 だが、今回の客には少々驚きを隠せなかった。目の前に現れたのは小さな女の子だった。背は魔理沙と同じくらいか気持ち小さいかもしれない。そこまでは普通である。だが、ぼろぼろに朽ちた羽、ふわふわした白い衣装……それにこの子が来てからほのかに部屋の中が暖かくなったような気がする……そう、その女の子は紛れもなく妖精だった。 妖精は基本的に清潔な水と空気があれば生きていけると言われている。つまり一般的には妖精は物欲の存在しない生き物、つまりこんなかび臭い古道具屋とは無縁の生き物だと思っていたが、その古道具屋に一体何の用なのだろうか。 ……おっと、驚きすぎて我を忘れていたようだ。余り呆けた顔を見せ付けては客に失礼だ。 「何かお探し物ですか、妖精さん」 とりあえず、普通に応対してみることにした。 「あの…春を伝えにきました」 そこで僕はもう一度目を丸くした。彼女は突然目の前で服を脱ぎだしたではないか。帽子を脱ぎ…ふわりとした上着を脱ぎ…彼女が肌着に手をかけたところで僕は我に返り慌ててそれを制した。 その後、僕は彼女に茶菓子を出し、春の本当の意味をゆっくり教えてあげると彼女は赤面し、慌てて恥ずかしそうに去っていった。 あのまま脱いでいく様を止めるべきではなかったのかもしれない……僕の中に少し後悔の念が現れたが、説得の末彼女の下着をもらえただけでもまぁ、よしとしようではないか。今夜はぐっすり眠れそうだ。</p>","frontmatter":{"cover":null,"date":"2005-04-12T02:58:29+09:00","strDate":"2005/4/11","title":"本文"}}},"pathContext":{"path":"/ja/blog/2005/04/12-本文-3/","langKey":"ja"}}

